/* From uiverse.io by @WhiteNervosa */
.LandingPage_button__2U_Ay button {
    font-size: 24px;
    color: #e1e1e1;
    text-shadow: 0 0 30px black, 0 0 3px black;
    font-weight: 800;
    cursor: pointer;
    position: relative;
    border: none;
    background: none;
    transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
    transition-duration: 400ms;
    transition-property: color;
  }
  
  .LandingPage_button__2U_Ay button:focus,
  .LandingPage_button__2U_Ay button:hover {
    color: #9ab64b;
  }
  
  .LandingPage_button__2U_Ay button:focus:after,
  .LandingPage_button__2U_Ay button:hover:after {
    width: 100%;
    left: 0%;
  }
  
  .LandingPage_button__2U_Ay button:after {
    content: "";
    pointer-events: none;
    bottom: -2px;
    left: 50%;
    position: absolute;
    width: 0%;
    height: 2px;
    background-color: #9ab64b;
    transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
    transition-duration: 400ms;
    transition-property: width, left;
  }
