/* From uiverse.io by @G4b413l */
.Loader_leap_frog__UuB2t {
    --uib-size: 40px;
    --uib-speed: 2s;
    --uib-color: #9ab64b;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: var(--uib-size);
    height: var(--uib-size);
   }
   
   .Loader_leap_frog__dot__35hMz {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    height: 100%;
   }
   
   .Loader_leap_frog__dot__35hMz::before {
    content: '';
    display: block;
    height: calc(var(--uib-size) * 0.22);
    width: calc(var(--uib-size) * 0.22);
    border-radius: 50%;
    background-color: var(--uib-color);
    will-change: transform;
   }
   
   .Loader_leap_frog__dot__35hMz:nth-child(1) {
    -webkit-animation: Loader_leapFrog__2kpBZ var(--uib-speed) ease infinite;
            animation: Loader_leapFrog__2kpBZ var(--uib-speed) ease infinite;
   }
   
   .Loader_leap_frog__dot__35hMz:nth-child(2) {
    -webkit-transform: translateX(calc(var(--uib-size) * 0.4));
            transform: translateX(calc(var(--uib-size) * 0.4));
    -webkit-animation: Loader_leapFrog__2kpBZ var(--uib-speed) ease calc(var(--uib-speed) / -1.5)
       infinite;
            animation: Loader_leapFrog__2kpBZ var(--uib-speed) ease calc(var(--uib-speed) / -1.5)
       infinite;
   }
   
   .Loader_leap_frog__dot__35hMz:nth-child(3) {
    -webkit-transform: translateX(calc(var(--uib-size) * 0.8)) rotate(0deg);
            transform: translateX(calc(var(--uib-size) * 0.8)) rotate(0deg);
    -webkit-animation: Loader_leapFrog__2kpBZ var(--uib-speed) ease calc(var(--uib-speed) / -3)
       infinite;
            animation: Loader_leapFrog__2kpBZ var(--uib-speed) ease calc(var(--uib-speed) / -3)
       infinite;
   }
   
   @-webkit-keyframes Loader_leapFrog__2kpBZ {
    0% {
     -webkit-transform: translateX(0) rotate(0deg);
             transform: translateX(0) rotate(0deg);
    }
   
    33.333% {
     -webkit-transform: translateX(0) rotate(180deg);
             transform: translateX(0) rotate(180deg);
    }
   
    66.666% {
     -webkit-transform: translateX(calc(var(--uib-size) * -0.4)) rotate(180deg);
             transform: translateX(calc(var(--uib-size) * -0.4)) rotate(180deg);
    }
   
    99.999% {
     -webkit-transform: translateX(calc(var(--uib-size) * -0.8)) rotate(180deg);
             transform: translateX(calc(var(--uib-size) * -0.8)) rotate(180deg);
    }
   
    100% {
     -webkit-transform: translateX(0) rotate(0deg);
             transform: translateX(0) rotate(0deg);
    }
   }
   
   @keyframes Loader_leapFrog__2kpBZ {
    0% {
     -webkit-transform: translateX(0) rotate(0deg);
             transform: translateX(0) rotate(0deg);
    }
   
    33.333% {
     -webkit-transform: translateX(0) rotate(180deg);
             transform: translateX(0) rotate(180deg);
    }
   
    66.666% {
     -webkit-transform: translateX(calc(var(--uib-size) * -0.4)) rotate(180deg);
